                    >
                      <AppIcon
                        name="mail"
                        class="w-2.5 h-2.5 mr-0.5"
                      />rita860620@gmail.com</a>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <!-- footer -->
          <footer class="bg-gray-light-600 dark:bg-gray-dark-300 py-2">
            <div class="container flex items-center justify-between">
              <div class="flex items-center text-sm">
                <div>
                  看更多版本：
                </div>
                <router-link
                  v-for="(item, index) in versionListFiltered"
                  :key="index"
                  :to="item.link"
                  :class="$style.linkText"
                >
                  <AppIcon
                    name="link"
                    :class="$style.iconLink"
                  />{{ item.title }}
                </router-link>
              </div>
              <span class="text-xs"> Copyright © 2022 Rita. All rights reserved.</span>
            </div>
          </footer>

          <!-- style mode -->
          <div class="fixed left-2 bottom-2">
            <div
              class="flex flex-col items-center justify-center bg-secondary-light dark:bg-secondary-dark rounded-full p-1.5
         ease-in duration-300 opacity-50 hover:opacity-100"
            >
              <AppIcon
                v-for="(mode, index) in styleModeList"
                :key="mode"
                :name="mode"
                class="w-3 h-3 cursor-pointer transition hover:transform hover:rotate-45"
                :class="[{'mb-2': index!==styleModeList.length-1},
                         [styleModeActive === mode ? 'text-white' : 'text-dark/50']]"
                @click="setStyleModeActive(mode)"
              />
            </div>
          </div>

          <!-- back to top -->
          <div class="fixed right-2 bottom-2">
            <div
              class="flex flex-col items-center justify-center bg-secondary-light dark:bg-secondary-dark rounded-full p-1.5 cursor-pointer"
              @click="scrollToTop"
            >
              <AppIcon
                name="chevron-top"
                class="w-3 h-3 text-white transition hover:-translate-y-0.5"
              />
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script setup>
import { scrollToTop } from '@/helpers/useMixin'
import '@/assets/styles/tailwind.css'
import { defineProps } from 'vue'
import { ref } from '@vue/reactivity'
import { worksList, skillsList } from '@/helpers/useWorks'
import { versionListFiltered } from '@/helpers/useVersion'
import { onMounted, watch } from '@vue/runtime-core'
import { useRouter } from 'vue-router'

const router = useRouter()

const props = defineProps({
  styleModeActive: {
    type: String,
    default: 'sun'
  }
})

const styleModeList = ref(['sun', 'moon'])
const styleModeActive = ref(props.styleModeActive)
watch(props, () => {
  setStyleModeActive(props.styleModeActive)
})
const setStyleModeActive = (mode) => {
  styleModeActive.value = mode
  router.push(mode === 'sun' ? { path: '/version-01-light' } : { path: '/version-01-dark' })
}

const worksStyleMap = {
  'omniec-content-management': {
    imgType: 'horizontal'
  },
  'omniec-frontend': {
    imgType: 'vertical'
  },
  'omniec-admin': {
    imgType: 'horizontal'
  },
  'omniec-coolmama': {
    imgType: 'vertical'
  },
  'omniec-nana': {
    imgType: 'vertical'
  },
  'omniec-atenore': {
    imgType: 'horizontal'
  }
}
const skillsStyleMap = {
  vue: { icon: 'vue' },
  sass: { icon: 'sass' },
  git: { icon: 'git' }
}

onMounted(() => {
  // remove bootstrap class
  document.documentElement.className = ''
})

</script>

<style lang="scss" module>
@import url("https://fonts.googleapis.com/css2?family=Rosario&display=swap");

.container {
  font-family: Rosario, ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

:global(.transition),
.transition {
  @apply ease-[cubic-bezier(0.37,0,0.63,1)] duration-300;
}

.card {
  @apply relative;
  @apply bg-gray-light-600;
  @apply overflow-hidden rounded-xl;
  @apply grid grid-cols-1 lg:grid-cols-2;
  @apply px-3 md:px-8 mb-5;

  .imageWrap {
    @apply h-2/5-screen lg:h-full;
    @apply relative;
    @apply overflow-hidden;
  }

  .img {
    @apply absolute;
    @apply shadow-xl;
    @apply transition;
  }

  .vertical .img {
    @apply h-auto;

    &:nth-child(1) {
      @apply z-10;
      @apply w-1/2;
      @apply object-top;
      @apply top-1/4;
    }

    &:nth-child(2) {
      @apply w-1/3;
      @apply object-bottom;
      @apply bottom-1/5;
    }
  }

  .horizontal .img {
    @apply h-auto;

    &:nth-child(1) {
      @apply z-10;
      @apply w-2/3;
      @apply bottom-0 translate-y-1/4;
    }

    &:nth-child(2) {
      @apply w-3/5;
      @apply bottom-1/4;
    }
  }

  &:nth-child(odd) {
    .vertical .img {
      &:nth-child(1) {
        @apply right-1/2 translate-x-1/3;
        @apply lg:left-0 lg:translate-x-1/2;
      }

      &:nth-child(2) {
        @apply left-1/2;
        @apply lg:left-0 lg:translate-x-3;
      }
    }

    .horizontal .img {
      &:nth-child(1) {
        @apply right-1/2 translate-x-1/3;
        @apply lg:right-8 lg:translate-x-0;
      }

      &:nth-child(2) {
        @apply left-1/2;
        @apply lg:left-0 lg:translate-x-3;
      }
    }
  }

  &:nth-child(even) {
    .imageWrap {
      @apply lg:order-last;
    }

    .vertical .img {
      &:nth-child(1) {
        @apply right-1/2 translate-x-1/3;
        @apply lg:right-0 lg:-translate-x-1/2;
      }

      &:nth-child(2) {
        @apply left-1/2;
        @apply lg:left-auto lg:right-0 lg:-translate-x-3;
      }
    }

    .horizontal .img {
      &:nth-child(1) {
        @apply left-8;
      }

      &:nth-child(2) {
        @apply right-0 -translate-x-3;
      }
    }
  }

  &:hover {
    .vertical .img {
      &:nth-child(1) {
        @apply top-1/5;
      }

      &:nth-child(2) {
        @apply bottom-1/4 translate-x-0;
      }
    }

    .horizontal .img {
      &:nth-child(1) {
        @apply translate-y-0;
      }

      &:nth-child(2) {
        @apply bottom-1/3 translate-x-0;
      }
    }
  }
}

.tag {
  @apply rounded-full;
  @apply px-1 py-0.25 mr-1 mb-1;
  @apply border border-secondary-light/50;
  @apply text-sm;
  @apply select-none;
}

.iconLink {
  @apply fill-current;
  @apply w-2.5 h-2.5;
  @apply mr-0.5;
}

.linkText {
  @apply text-text-light;
  @apply no-underline;
  @apply rounded-full;
  @apply inline-flex items-center;
  @apply px-1 py-1;
  @apply border border-transparent;
  @apply transition;
  @apply hover:text-text-light/50 hover:-translate-y-0.25;
}

.link {
  @apply text-text-light;
  @apply no-underline;
  @apply rounded-full;
  @apply inline-flex items-center;
  @apply px-2 py-1;
  @apply -translate-x-2;
  @apply border border-transparent;
  @apply transition;
  @apply hover:text-text-light/50 hover:-translate-y-0.25;
}

:global(.dark) {
  .card {
    @apply bg-gray-dark-300;
  }

  .tag {
    @apply border-white/25;
  }

  .linkText,
  .link {
    @apply text-white;
    @apply hover:text-text-dark/50;
  }
}
</style>
